/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-lone-blocks */

import { useState, useEffect, useRef } from 'react'
import { Grid, Box, Button, TextField, IconButton, Tooltip, MenuItem } from '@material-ui/core'
import { withStyles } from '@material-ui/styles'
import {
  Description as ExportIcon,
  FileCopy as FileCopyIcon
} from '@material-ui/icons'
import { useHistory } from 'react-router-dom'
import { connect } from 'react-redux'

import { setCurrentTab, setCurrentCollection } from '@/Logic/redux'
import { pickCardFields } from '@/Api/MagicdexApi/utils'
import { BaseForm } from '@/Components'
import useStyles from './styles'


/** REDUX **/
const mapStateToProps = (state) => ({
  username: state.actions.activeUser.username,
  collection: state.actions.activeUser.collection,
})

const mapDispatchToProps = (dispatch) => ({
  dispatch: {
    setCurrentTab: (tab) => dispatch(setCurrentTab({ tab })),
    setCurrentCollection: (collection) => dispatch(setCurrentCollection({ collection })),
  }
})


const Export = ({
  /** VARS **/
  ...props
}) => {
  const {
    classes,
    dispatch,
    username,
    collection,
  } = props
  const history = useHistory()
  const textfieldInputRef = useRef()
  const [selectedExportType, setSelectedExportType] = useState('magicdex')
  const [cardListText, setCardListText] = useState('')
  const [textfieldHelperText, setTextfieldHelperText] = useState(' ')


  /** EFFECTS **/
  useEffect(() => {
    //onMount
    dispatch.setCurrentTab('collection')
  }, [])

  useEffect(() => {
    if (!username)
      return history.push('/login')
  }, [username])


  /** HANDLERS **/
  const handleSubmit = () => {
    setTextfieldHelperText(' ')
    
    // TODO: handle form submit
    setCardListText(
      `selected: ${selectedExportType}`
    )
  }

  const handleReset = () => {
    setSelectedExportType('magicdex')
    setCardListText('')
    setTextfieldHelperText(' ')
  }

  const handleTextFocus = (e) => {
    if (cardListText) {
      e.currentTarget.select()
      navigator.clipboard.writeText(cardListText)
      setTextfieldHelperText('Text copied to clipboard!')
    }
  }


  /** RENDER **/
  return (
    <div className={classes.root}>
      <BaseForm
        onSubmit={handleSubmit}
        instantValidate={false}
        style={{ width: '100%' }}

        header='Bulk Export'
        headerProps={{ align: 'left' }}
        icon={<ExportIcon style={{ fontSize: '1em' }} />}

        content={() => (
          <Grid container justifyContent='center' alignItems='center' spacing={3}>
            <Grid item xs={7}>
              <TextField select
                color='secondary'
                variant='outlined'
                label='Export type'
                size='small'
                margin='dense'
                value={selectedExportType}
                onChange={(e) => setSelectedExportType(e.target.value)}
                style={{ textAlign: 'start' }}
              >
                {
                  ['MagicDex', 'CSV'].map((option) => (
                    <MenuItem key={option} value={option.toLowerCase()}>
                      {option}
                    </MenuItem>
                  ))
                }
              </TextField>
            </Grid>
            <Grid item xs={6}>
              <TextField multiline fullWidth
                disabled={!cardListText}
                inputRef={textfieldInputRef}
                onFocus={handleTextFocus}
                color='secondary'
                variant='filled'
                rows={20}
                label='Card List'
                value={cardListText}
                helperText={textfieldHelperText}
                InputProps={{
                  readOnly: true,
                  endAdornment: (
                    <Tooltip title='Copy' placement='right'>
                      <IconButton disabled={!cardListText} size='small' onClick={() => textfieldInputRef.current.focus()} style={{ alignSelf: 'flex-start' }}>
                        <FileCopyIcon style={{ fontSize: '1.25em' }} />
                      </IconButton>
                    </Tooltip>
                  )
                }}
              />
            </Grid>
          </Grid>
        )}

        actions={() => (
          <Box paddingTop={2} marginLeft={1.5} marginRight={1.5}>
            <Grid container justifyContent='flex-end' alignItems='center' spacing={1}>
              <Grid item xs align='left'>
                <Button variant='contained' color='secondary' onClick={handleReset}>
                  Reset
                </Button>
              </Grid>
              <Grid item>
                <Button variant='contained' type='submit' color='primary'>
                  Submit
                </Button>
              </Grid>
            </Grid>
          </Box>
        )}
      />
    </div>
  )
}

/** EXPORT **/
export default
  withStyles(useStyles)(
    connect(mapStateToProps, mapDispatchToProps)(
      Export
    )
  )